//#region CRYPTO
async function RSAESEncryptionString(str) {
    try {
      const publicKey = await GetPublicKey(); //   
      if (!publicKey) {
        throw new Error('Failed to get public key');
      }
  
      const textEncoder = new TextEncoder();
      const encryptedBuffer = await window.crypto.subtle.encrypt(
        {
          name: 'RSA-OAEP'
        },
        publicKey,
        textEncoder.encode(str)
      );
  
      // Uint8Array Base64  
      const encryptedArray = new Uint8Array(encryptedBuffer);
      const base64String = arrayBufferToBase64(encryptedArray);
      return base64String;
    } catch (error) {
      console.error('Encryption error:', error);
      return null;
    }
  }
  
  // ArrayBuffer Base64   
  function arrayBufferToBase64(buffer) {
    const binary = String.fromCharCode(...new Uint8Array(buffer));
    return btoa(binary);
  }
  
  //    
  async function GetPublicKey() {
    try {
      const response = await fetch(`${SVR_URL}/login/getpublicKey`, {
        method: "post",
        headers: { "content-type": "application/json" },
      });
      const publicKeyPEM = await response.text();
      const publicKey = await importPublicKey(publicKeyPEM);
      return publicKey;
    } catch (error) {
      console.error('Failed to fetch public key:', error);
      return null;
    }
  }
  
  // PEM  CryptoKey   
  async function importPublicKey(pemKey) {
    const pemString = pemKey
      .replace('-----BEGIN PUBLIC KEY-----', '')
      .replace('-----END PUBLIC KEY-----', '')
      .replace(/\s/g, ''); //    
  
    const binaryString = window.atob(pemString);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; ++i) {
      bytes[i] = binaryString.charCodeAt(i);
    }
  
    // console.log(`byte`,bytes);
    return await window.crypto.subtle.importKey(
      'spki',
      bytes.buffer,
      {
        name: 'RSA-OAEP',
        hash: 'SHA-256',
      },
      true,
      ['encrypt']
    );
  }
  //#endregion

module.exports = { selectedID, SVR_URL, mqttRun, opcuaRun, mqttAll, opcuaAll, accountCheck, nickNameCheck, IDCheck, PWCheck, selectedUserID, selectedUserNickname, selectedUserPW, myID, myNN, myRole,loginstate,RSAESEncryptionString,InformationMSG };