import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Instance from "../../Variable/Instance.js";

//   
const BaseBG = styled.div`
    width: 25%;
    height: 35%;
    min-height: 17vmax;
    background: radial-gradient(ellipse at top, #0088F4 0%, #00508D 22%, #002E4D 100%);
    border: 0.05vmax solid white;
    padding: 1.5vmax;
    border-radius: 1vmax;
    margin-left: 1vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;
`
const ContentContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 1vmax;
`
// ' ...  '   
const LeftContainer = styled.div`
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
`
//     
const RightContainer = styled.div`
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
`
// ' ...  ' 
const Title = styled.div`
    display: flex;
    align-items: center;
    height: 2vmax;
    font-family: 'Pretendard', sans-serif;
    font-size: 1vmax;
    font-weight: 400;
    color: white;
    margin-bottom: 1vmax;
`
//     
const DataInput = styled.input`
    display: flex;
    height: 1.9vmax;
    font-family: 'Pretendard', sans-serif;
    font-size: 0.8vmax;
    font-weight: 300;
    background-color: #011428;
    border-radius: 0.3vmax;
    text-align: left;
    color: white;
    margin-bottom: 1vmax;
    padding-left: 1vmax;
    border: none;
`
const ErrContainer = styled.div`
    width: 100%;
    height: 8%;
    display: flex;
    align-items: center;
    justify-content: center;
`

const BtnContainer = styled.div`
    width: 100%;
    height: 13%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
`
const CommitBtn = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 100%;
    background: linear-gradient(to bottom, #56B4FF 0%, #0088F4 100%);
    border-radius: 0.5vmax;
    padding: 0.5vmax;
    font-family: 'Pretendard', sans-serif;
    font-weight: 700;
    font-size: 1.2vmax;
    color: white;
    margin-left: 1vmax;
    margin-right: 1vmax;
    cursor: pointer;
    border: none;
    &:hover {
        background: linear-gradient(to bottom, #067EDE 0%, #0862A9 100%);
    }
`
const CancelBtn = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 100%;
    background: none;
    border: 0.05vmax solid white;
    border-radius: 0.5vmax;
    padding: 0.5vmax;
    font-family: 'Pretendard', sans-serif;
    font-weight: 700;
    font-size: 1.2vmax;
    color: white;
    margin-left: 1vmax;
    margin-right: 1vmax;
    cursor: pointer;
    &:hover {
        background-color: #456C86;
    }
`
const ErrorMessage = styled.div`
  color: #ff1212;
  font-family: 'Pretendard', sans-serif;
  font-weight: 500;
  font-size: 0.9vmax;
`
function DataSetModal({ offChange }) {

    var [nickname, setNN] = useState(`${Instance.myNN}`);
    const [curpw, setCURPW] = useState('');
    const [npw, setNPW] = useState('');
    const [ncpw, setNCPW] = useState('');
    const [errorMsg, setErrorMsg] = useState(' ');

    //nickname = Instance.myNN;
    useEffect(() => {
        setErrorMsg(' ');
    }, []);

    async function ChangeMyInfo() {
        console.log(`npw : ${npw} / cpw : ${curpw}`)
        if (npw === ncpw) {
            if (Instance.nickNameCheck(nickname)) {
                if (Instance.PWCheck(npw)) {
                    const encCurPW = await Instance.RSAESEncryptionString(curpw);
                    const encNewPW = await Instance.RSAESEncryptionString(npw);
                    fetch(`${Instance.SVR_URL}/user/modify_myinfo`, {
                        method: "post",
                        headers: { "content-type": "application/json" },
                        body: JSON.stringify({
                            ID: Instance.myID,
                            CPW: encCurPW,
                            NPW: encNewPW,
                            NICKNAME: nickname
                        })
                    }).then(res => res.text())
                    .then(data => {
                        if (data === "MODIFY SUCCESS") {
                            //onClose();
                            window.location.reload();
                        }
                        else{
                            setErrorMsg(data);
                        }
                        Instance.selectedID = null;
                        console.log(`${data}`);
                    });
                    //window.location.reload();
                }
                else {
                    setErrorMsg(` .`);
                    console.log(`  .`)
                }
            }
            else {
                setErrorMsg(` .`);
                console.log(`   `);
            }
        }
        else {
            setErrorMsg(` .`);
            console.log(` .`)
        }
    }
    return (
        <BaseBG>
            <ContentContainer>
                <LeftContainer>
                    <Title></Title>
                    <Title> </Title>
                    <Title> </Title>
                    <Title> </Title>
                </LeftContainer>
                <RightContainer>
                    <DataInput onChange={(e) => setNN(e.target.value)} value={nickname} />
                    <DataInput type="password" onChange={(e) => setCURPW(e.target.value)} value={curpw} />
                    <DataInput type="password" onChange={(e) => setNPW(e.target.value)} value={npw} />
                    <DataInput type="password" onChange={(e) => setNCPW(e.target.value)} value={ncpw} />
                </RightContainer>
            </ContentContainer>
            <ErrContainer>
                <ErrorMessage>{errorMsg}</ErrorMessage>
            </ErrContainer>
            <BtnContainer>
                <CommitBtn onClick={ChangeMyInfo}></CommitBtn>
                <CancelBtn onClick={offChange}></CancelBtn>
            </BtnContainer>
        </BaseBG>
    )
}

export default DataSetModal;